import React, { useState, useEffect } from 'react';
import '../componets/log-in/login.css';
import { FiArrowRight, FiCheckCircle } from 'react-icons/fi';
import { supabase } from '../database/supabaseClient';
import { events } from '../content/events';

const i18n =  events.test

const EventPayment: React.FC = () => {
  const [language, setLanguage] = useState<'EN' | 'VN'>('EN');
  const [loading, setLoading] = useState(false);
  const [isPaid, setIsPaid] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [qrData, setQrData] = useState<any>(null);
  const t = i18n[language];

  useEffect(() => {
    supabase.auth.getUser().then(({ data }) => {
      if (data?.user) setUser(data.user);
    });
  }, []);

  // === Create VietQR Payment Intent ===
  const handlePayment = async () => {
    if (!user) {
      alert('Please log in first.');
      return;
    }

    const endpoint = 'https://integrations.tinhhoaviet.org.vn/vietqr/payment-intent';

    try {
      setLoading(true);
      const res = await fetch(endpoint, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ user_id: user.id }),
      });

      if (!res.ok) throw new Error('Payment request failed.');
      const data = await res.json();

      console.log('üí≥ VietQR API Response:', data);

      if (data.qrLink) {
        setQrData(data); // store the VietQR response
        window.open(data.qrLink, '_blank');
      } else {
        alert('Payment link not received.');
        setIsPaid(true);
      }
    } catch (err) {
      console.error('‚ùå Payment Error:', err);
      alert('An error occurred while initiating payment.');
    } finally {
      setLoading(false);
    }
  };

  // === Test Payment Simulation ===
  const handleTestPayment = async () => {
    if (!qrData) {
      alert('No QR data available.');
      return;
    }

    // Build payload2 dynamically
    const payload2 = {
      bankAccount: qrData.bankAccount,
      content: qrData.content,
      amount: qrData.amount,
      bankCode: qrData.bankCode,
      transType: 'C',
    };

    console.log('üßæ Sending payload2:', payload2);

    try {
      const res = await fetch('https://integrations.tinhhoaviet.org.vn/vietqr/test-success', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload2),
      });

      const result = await res.text();
      console.log('‚úÖ Response from test-success:', result);
    } catch (err) {
      console.error('‚ùå Failed to post to /vietqr/test-success:', err);
    }
  };

  return (
    <div className="auth-shell">
      {/* Left branding panel */}
      <aside className="brand-side">
        <div className="brand-sheen" />
        <div className="brand-pattern" />
        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>
          <ul className="brand-features">
            {t.features.map((f : any , i : number ) => (
              <li key={i} className="feature">
                <span className="tick">‚úì</span><span>{f}</span>
              </li>
            ))}
          </ul>
        </div>
      </aside>

      {/* Right form side */}
      <main className="form-side">
        <header className="form-top">
          <div className="lang-switch">
            <button
              className={`lang ${language === 'EN' ? 'active' : ''}`}
              onClick={() => setLanguage('EN')}
            >
              {t.langEN}
            </button>
            <span className="mid">|</span>
            <button
              className={`lang ${language === 'VN' ? 'active' : ''}`}
              onClick={() => setLanguage('VN')}
            >
              {t.langVN}
            </button>
          </div>
        </header>

        <section className="form-wrap" style={{ color: 'var(--ink)' }}>
          <div className="intro">
            <h2 className="title">{t.title}</h2>
            <p className="subtitle">{t.subtitle}</p>
          </div>

          <div className="auth-form" style={{ textAlign: 'center', color: 'var(--ink)' }}>
            <div className="field">
              <h3 style={{ color: 'var(--gold-2)', fontSize: 22, marginBottom: 6 }}>
                {t.eventName}
              </h3>
              <p>{t.date}</p>
              <p>{t.location}</p>
              <p style={{ fontWeight: 700, marginTop: 10 }}>{t.price}</p>
            </div>

            {/* Dynamic Button Behavior */}
            {!qrData ? (
              <button
                className="cta"
                onClick={handlePayment}
                disabled={loading || !user}
                style={{
                  opacity: loading || !user ? 0.6 : 1,
                  cursor: !user ? 'not-allowed' : 'pointer',
                }}
              >
                <span>
                  {!user
                    ? 'Login required'
                    : loading
                    ? t.loading
                    : t.payBtn}
                </span>
                {!loading && user && <FiArrowRight className="arrow" />}
              </button>
            ) : (
              <button
                className="cta"
                onClick={handleTestPayment}
                style={{ backgroundColor: '#00b37a' }}
              >
                {t.testBtn}
              </button>
            )}

            {isPaid && (
              <div
                style={{
                  marginTop: 20,
                  color: 'var(--gold-2)',
                  fontWeight: 700,
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  gap: 8,
                }}
              >
                <FiCheckCircle size={22} />
                {t.success}
              </div>
            )}
          </div>
        </section>
      </main>
    </div>
  );
};

export default EventPayment;
